.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;

  transition: opacity var(--timing-function),
  visibility var(--timing-function);
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%) scale(0.2);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  width: 280px;
  padding: 48px 20px 40px 20px;
  max-height: 98%;
  overflow: auto;

  z-index: 100;

  background-color: var(--secondaryColor);

  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);

  transform: translate(-50%, -50%) scale(1);
  transition: transform var(--timing-function);

  @include mq(tablet) {
    width: 704px;
    display: flex;
  }

  @include mq(desktop) {
    width: 806px;
    padding-left: 10px;
  }
}

.modal__button-close {
  position: absolute;
  padding: 0;
  top: 8px;
  right: 8px;

  width: 30px;
  height: 30px;
  background-color: transparent;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M8 8L22 22' stroke='black' stroke-width='2'/%3e%3cpath d='M8 22L22 8' stroke='black' stroke-width='2'/%3e%3c/svg%3e ");
  border-radius: 50%;
  // fill: #b70404;
  // transform: scale(1);
  transition: transform var(--timing-function);
}

.modal__button-close:hover,
.modal__button-close:focus {
  // transform: scale(1.15);
}

.modal__icon-close {
  height: 30px;
  width: 30px;
  margin: 0;
  padding: 0;
  z-index: 1;
}

.modal__icon-close use {
  height: 30px;
  width: 30px;
  margin: 0;
  padding: 0;
  z-index: 1;
}

